"use strict";

import slider from "./modules/slider.js";
import preloader from "./modules/preloader.js";
import resize from "./services/resize.js";

import {
	changeDonecTextarea,
	changeFooterTitle,
	changeTelMail,
	changeTitleModal,
} from "./services/changeDomElements.js";
import scroll from "./services/scroll.js";
import menuScroll from "./modules/menu.js";
import click from "./services/click.js";
import { hamburgerClick, requestClick } from "./services/clickFunctions.js";

document.addEventListener("DOMContentLoaded", () => {
	changeDonecTextarea();
	changeTitleModal();

	slider(".new-slider__swiper-container");
	preloader();

	resize(changeDonecTextarea, changeTitleModal, changeTelMail);

	scroll(() => menuScroll("modifikator"));

	click((e) => hamburgerClick(e, "modifikator"), requestClick);
});

