const data = [
	{ id: 1, category: "A", name: "Item 1" },
	{ id: 2, category: "B", name: "Item 2" },
	{ id: 3, category: "A", name: "Item 3" },
	{ id: 4, category: "C", name: "Item 4" },
	{ id: 5, category: "B", name: "Item 5" },
];

let result = data.reduce((acc, item) => {
	if (!acc[item.category]) {
		acc[item.category] = [];
	}
	acc[item.category].push(item);
	return acc;
}, {});
console.log(result);

function groupByKey(arr, key) {
	return arr.reduce((acc, item) => {
		if (!acc[item[key]]) {
			acc[item[key]] = [];
		}
		acc[item[key]].push(item);
		return acc;
	}, {});
}

console.log(groupByKey(data, "category"));

//     .     .reduce()     .
const numbers = [10, 20, 30, 40, 50];

let result2 = numbers.reduce((acc, num) => acc + num, 0) / numbers.length;
console.log(result2); // 30

//     ,       :   .     .reduce()       .
const items = [
	{ name: "Apple", price: 1.2 },
	{ name: "Banana", price: 0.8 },
	{ name: "Orange", price: 1.5 },
	{ name: "Grapes", price: 2.0 },
];

let totalPrice = items.reduce((acc, item) => acc + item.price, 0);
console.log(totalPrice); // 5.5

//     ,     .     .reduce()  ,         .

const names = ["Alice", "Bob", "Alice", "Charlie", "Bob", "Alice"];

let num = names.reduce((acc, name) => {
	if (!acc[name]) {
		acc[name] = 0;
	}
	acc[name] = acc[name] + 1;
	return acc;
}, {});

console.log(num);

//     ,           .     .reduce()     .

const orders = [
	{ item: "Laptop", quantity: 2, price: 1000 },
	{ item: "Phone", quantity: 1, price: 500 },
	{ item: "Tablet", quantity: 3, price: 300 },
	{ item: "Headphones", quantity: 4, price: 50 },
];

const sum = orders.reduce(
	(acc, order) => acc + order.quantity * order.price,
	0
);
console.log(sum); // 4100

//     ,        .     .reduce()     .

const people = [
	{ name: "Alice", age: 25 },
	{ name: "Bob", age: 30 },
	{ name: "Charlie", age: 35 },
	{ name: "David", age: 40 },
];

const mean =
	people.reduce((acc, person) => acc + person.age, 0) / people.length;
console.log(mean); // 32.5
